html{background-color:#0d1117;color:#c9d1d9;font-family:"Open Sans","Segoe UI",sans-serif;font-size:4vh}body,html{padding:0;margin:0}#root,.clock{display:flex;justify-content:center}#root,body,html{height:100%;width:100%}#root{align-items:center}.clock{flex-direction:row;align-items:flex-start;transform:translateY(22.5vh)}.clock-row,.colon{margin:0 2px}.clock-row{border:1.5px solid #c9d1d9;border-radius:2px;padding:5px 10px;transition:transform cubic-bezier(.17,.67,.83,.67) .3s}.clock-row.active-child-0{transform:translateY(0)}.clock-row.active-child-1{transform:translateY(-5vh)}.clock-row.active-child-2{transform:translateY(-10vh)}.clock-row.active-child-3{transform:translateY(-15vh)}.clock-row.active-child-4{transform:translateY(-20vh)}.clock-row.active-child-5{transform:translateY(-25vh)}.clock-row.active-child-6{transform:translateY(-30vh)}.clock-row.active-child-7{transform:translateY(-35vh)}.clock-row.active-child-8{transform:translateY(-40vh)}.clock-row.active-child-9{transform:translateY(-45vh)}.clock-row *{line-height:5vh;border-radius:100%}.active{color:#275efe;transition:color cubic-bezier(.17,.67,.83,.67) .3s}@font-face{font-family:"Open Sans";src:local("Open Sans"),local("Open-Sans"),url(/fonts/OpenSans-Light.woff2) format("woff2")}